@use 'base/variables' as v;
@use 'base/mixins' as m;


.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        background: rgb(126,2,195);
        background: linear-gradient(270deg, rgba(126,2,195,0.5) 0%, rgba(255, 0, 179, 0.3) 100%); 
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video {
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%;
        }
    }
}

.content-video {
    z-index: 10;
    h2{
        color: v.$white;
        font-size: 7rem;
        margin: 0;
    }

    p {
        background-color: v.$pink;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$white;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;

    }
}