@use 'base/variables' as v;
@use 'base/mixins' as m;

.tickets{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$white;
        margin-bottom: 2rem;
    }

    .container-tickets{
        @include m.tablet{
            @include m.grid(2, 5rem)
        }
    }
}

.pass{
    .pass-name{
        text-align: center;
        font-size: 2.4rem;
        color: v.$white;
    }

    &-header,
    &-footer{
        padding: .5rem;
        p{
            text-align: center;
            color: v.$white;
            margin: 0;
        }
    }


    &-header{
        p{
            text-transform: uppercase;
            font-size: 1.6rem;
        }
        background-color: v.$pink;
    }
    &-body{
        background-color: v.$white;
        ul{
            @include m.list;
            li,:nth-child(odd){
                background-color: v.$white;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }

    }
    &-footer{
        p{
            font-size: 2.4rem;
            font-weight: 700;
        }
        background-color: v.$purple;

    }
}